import React from "react"
import JSONData from "../../content/My-JSON-Content.json"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faExternalLinkAlt, faExternalLinkSquareAlt } from '@fortawesome/free-solid-svg-icons'
import { faGithub, faGithubSquare } from '@fortawesome/free-brands-svg-icons'
import '../scss/index.scss'
import JSONarc from "../../content/archive.json"

const JSONbuildtime = () => (
  <div style={{ maxWidth: `960px`, margin: `1.45rem` }}>
    <h1>{JSONData.title}</h1>
    <ul>
      {JSONData.content.map((data, index) => {
        return <li key={`content_item_${index}`}>{data.item}</li>
      })}
    </ul>
    <table class="table table-default">
      <thead>
        <tr>
          <th scope="col">Year</th>
          <th scope="col">Title</th>
          <th scope="col">Made with</th>
        </tr>
        </thead>
        <tbody>
            {JSONarc.content.map((data,index) =>{
              return <tr><th>{data.year}</th><td>{data.title}</td><td><span class="techused-font">{data.madewith}</span></td><td><a href="{data.github}"><FontAwesomeIcon icon={ faGithub }/></a> if ({data.demolink.length} {'>'} 0) {<a href="https://goog.com">{data.demolink}</a>} </td></tr>
            })}

        </tbody>
    </table>
  </div>
)
export default JSONbuildtime
